<template>
    <div id="sh-video" ref="video" @mouseenter="enter" @mouseleave="leave">
        <video :src="dataList[videoIndex].Path" ref="vdom" controls="controls" width="100%" height="100%">
        your browser does not support the video tag
        </video>
        <div v-show="flag1" class="sh-video-logo" >
            <div class="animate box"  @click="play">
                <span>TinySoft</span>
                <span  v-show="flag1" class="iconfont icon-bofang1"></span>
                <span  v-show="!flag1" class="iconfont icon-bofang2"></span>
            </div>
        </div>
        <div :class="flag?'setOut1 sh-video-footer':'setOut2 sh-video-footer'  " v-show="flag"> 
            <div class="sh-video-footer-top">
                <p>
                    <span>{{ regTime }}</span>
                    <span>{{ endTime }}</span>
                </p>
                <div class="block">
                    <el-slider v-model="value1" @change="setCompleteTime" :show-tooltip="false"></el-slider>
                </div>
            </div>
            <div class="sh-video-footer-button">
                <el-row>
                    <el-col :span="8"><div class="grid-content bg-purple">
                        <ul>
                            <li><i class="iconfont icon-CaretCircleLeft" @click="setup"></i></li>
                            <li v-show="flag1"><i class="iconfont icon-bofang1" @click="play"></i></li>
                            <li v-show="!flag1"><i class="iconfont icon-bofang2" @click="play"></i></li>
                            <li><i class="iconfont icon-CaretCircleRight" @click="setdown"></i></li>
                        </ul>
                    </div></el-col>
                    <el-col :span="12"><div class="grid-content bg-purple-light">
                        <ul>
                            <li class="playbackRate-li" @click="playbackRate">X<i>{{ value3 }}</i></li>
                            <li v-show="flag2" @mouseenter="enter1" @mouseleave="leave1">
                                <div v-show="flag7" class="yinliang">
                                    <div class="block">
                                        <el-slider
                                        v-model="value2"
                                        @change="setyinliang"
                                        vertical
                                        height="100px">
                                        </el-slider>
                                    </div>
                                </div>
                                <i class="iconfont icon-yinliang3" @click="yinliang"></i>
                            </li>
                            <li v-show="!flag2"><i class="iconfont icon-yinliangguanbi" @click="yinliang"></i></li>
                            <li v-show="flag3.value == 1"><i class="iconfont icon-CounterClockwise" :title="flag3.title" @click="playType(2)"></i></li>
                            <li v-show="flag3.value == 2"><i class="iconfont icon-Clockwise" :title="flag3.title" @click="playType(3)"></i></li>
                            <li v-show="flag3.value == 3"><i class="iconfont icon-sClockwise" :title="flag3.title" @click="playType(1)"></i></li>
                            <li v-show="!dataList[videoIndex].sc && isuser"><i class="iconfont icon-shouc" @click="shoucang"></i></li>
                            <li v-show="dataList[videoIndex].sc && isuser"><i class="iconfont icon-pingfen" @click="shoucang"></i></li>
                            <li v-show="!dataList[videoIndex].dz && isuser"><i class="iconfont icon-shoucang2" @click="shliev"></i></li>
                            <li v-show="dataList[videoIndex].dz && isuser"><i class="iconfont icon-shoucang5" @click="shliev"></i></li>
                            <li><i class="iconfont icon-xiaoxi1" @click="outerVisible = !outerVisible"></i></li>
                        </ul>
                    </div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">
                        <ul>
                            <li v-show="flag6"><i class="iconfont icon-quanping" @click="setout"></i></li>
                            <li v-show="!flag6"><i class="iconfont icon-fullscreen-exit" @click="setout"></i></li>
                        </ul>
                    </div></el-col>
                </el-row>
            </div>
        </div>
        <el-dialog title="用户评价信息" 
        :visible.sync="outerVisible"
        width="30%"
        >
            <el-form label-position="left" label-width="80px" :model="formLabelAlign">
                <el-form-item label="评价">
                    <el-rate v-model="formLabelAlign.count"></el-rate>
                </el-form-item>
                <el-form-item label="内容">
                    <el-input type="textarea" placeholder="请输入内容" v-model="formLabelAlign.text" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="outerVisible = false">取 消</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {mapState,mapMutations} from'vuex'
import {int2time} from '@/untils/tool'
export default {
  name: 'shVideo',
  data () {
    return {
        value1: 0,
        value2: 30,
        value3: 1,
        regTime: '00:00',
        endTime: '00:00',
        flag: false,
        //flag1: true,
        flag2: true,
        flag3: {"value":1,"title":"仅一次"},
        flag6: true,
        flag7: false,
        formLabelAlign: {
            count: 0,
            text: ''
        },
        outerVisible: false,
        myVideo: null,
        isuser: true,   //用户是否登录
    }
  },
  created(){
    this.init();
  },
  computed: {
    ...mapState({
        dataList: (state)=> {
            for(var i = 0;i<state.VideoData.videoDataInfo.length;i++){
                state.VideoData.videoDataInfo[i]["Path"] = "http://www.tinysoft.com.cn/videos/files/天软培训1/" + state.VideoData.videoDataInfo[i]["Path"];
            }
            return state.VideoData.videoDataInfo;
        },
        videoIndex: state=> state.VideoData.videoActiveIndex,
        flag1: state=> state.VideoData.flag1,
    }),
  },
  watch:{
    myVideo:function(){
        console.log("video 对象发生改变");
        let $this = this;
        $this.myVideo.addEventListener('error', function(event) {
            switch(this.error.code){
                case 1:console.log("ERRER::用户取消了视频的载入");break;
                case 2:console.log("ERRER::网络故障，造成数据载入失败");break;
                case 3:console.log("ERRER::解码错误，请重新访问");break;
                case 4:console.log("ERRER::浏览器不支持获得视频格式");break;
                default:console.log("ERRER::视频初始化错误");
            }
        },false);
        $this.myVideo.addEventListener('ended', function () { //结束
            //关闭播放按钮
            $this.setflag1(true);
            //进度条
            $this.value1 = 100;
            //时间
            $this.regTime = $this.endTime;
            if($this.flag3.value == 2){
                $this.play();
            }else if($this.flag3.value == 3){
                $this.setdown();
            }else{}
        }, false);
        $this.myVideo.oncanplay = function(){   //开始
            //总时长
            $this.endTime = int2time($this.myVideo.duration);
            //修改时间为0
            $this.regTime = '00:00',
            //修改进度为0
            $this.value1 = 0;
            if(!$this.flag1){
                $this.myVideo.play();
            }
        }
    }
  },
  methods: {
    ...mapMutations("VideoData", ["setvideoActiveIndex","setflag1","setflag4","setflag5"]),
    enter(){
        this.flag = true;
    },
    leave(){
        this.flag = false;
    },
    enter1(){
        this.flag7 = true;
    },
    leave1(){
        this.flag7 = false;
    },
    setCompleteTime(){
        let time = this.myVideo.duration * (this.value1 / 100);
        this.myVideo.currentTime = time;
        this.regTime = int2time(time);
    },
	init(){/*初始化*/
        let $this = this;
        setInterval(function(){
			if(!$this.myVideo.paused){
				$this.regTime = int2time($this.myVideo.currentTime);
                $this.value1 = 100 * ($this.myVideo.currentTime / $this.myVideo.duration);
			}
		},1500);
	},
    setup(){
        if(this.videoIndex){
            this.setvideoActiveIndex(this.videoIndex - 1);
        }else{
            this.setvideoActiveIndex(this.dataList.length - 1);
        }
    },
    play(){
        this.setflag1(!this.flag1);
        if(this.flag1){
            this.myVideo.pause();
        }else{
            this.myVideo.play();
        }
    },
    setdown(){
        if(this.videoIndex == this.dataList.length - 1){
            this.setvideoActiveIndex(0);
        }else{
            this.setvideoActiveIndex(this.videoIndex + 1);
        }
    },
    playbackRate(){
        this.value3 += 0.5;
        if(this.value3 > 2){
            this.value3 = 0.5;
        }
        this.myVideo.playbackRate = this.value3;
    },
    setyinliang(){
        if(this.value2){
            this.myVideo.volume = this.value2 / 100;
        }else{
            this.myVideo.volume = 0;
            this.yinliang();
        }
    },
    yinliang(){
        this.flag2 = !this.flag2;
        if(this.flag2){
            this.myVideo.volume = this.value2 / 100;
        }else{
            this.myVideo.volume = 0;
        }
    },
    playType(val){
        let $this = this;
        switch(val) {
          case 1:
              this.flag3 = {"value":1,"title":"仅一次"};
              break;
          case 2:
              this.flag3 = {"value":2,"title":"当前循环"};
              break;
          case 3:
              this.flag3 = {"value":3,"title":"列表循环"};
              break;
          default:
              alert("ERRER:: 获取播放类型失败");
        } 
    },
    shoucang(){
        this.setflag4();
    },
    shliev(){
        this.setflag5();
    },
    setout(){
        let ele = this.$refs['video'];
        if(this.flag6){
            if (ele.requestFullscreen) {
                ele.requestFullscreen();
            } else if (ele.mozRequestFullScreen) {
                ele.mozRequestFullScreen();
            } else if (ele .webkitRequestFullScreen) {
                ele.webkitRequestFullScreen();
            }
        }else{
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen();
            } else if (document.webkitCancelFullScreen) {
                document.webkitCancelFullScreen();
            }
        }
        this.flag6 = !this.flag6;
      }
  },
  mounted(){
      this.myVideo = this.$refs['vdom'];
  }
}
</script>

<style scopend lang="less">
#sh-video{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    -moz-user-select:none;/*火狐*/
    -webkit-user-select:none;/*webkit浏览器*/
    -ms-user-select:none;/*IE10*/
    -khtml-user-select:none;/*早期浏览器*/
    user-select:none;
    video::-webkit-media-controls{
        display:none !important;
    }
    video{
        background: #333;
    }
    >.sh-video-logo{
        margin: 0 auto; /*水平居中*/
        position: absolute;
        top: 40%; /*偏移*/
        left: 50%; /*偏移*/
        transform: translate(-50%,-50%);
        width: 150px;
        height: 150px;
        /*border: 1px solid #ddd;
        border-radius: 10px;*/
        padding: 15px;
        .box{
            animation-name: example;
            animation-duration: 2s;
            cursor:pointer;
            >.iconfont{
                font-size: 30px;
            }
            >span{
                font-family: "times new roman", times, serif;
                color: #F21F1F;
                font-size: 20px;
                font-style: italic;
                text-align: center;
                letter-spacing: 2.2pt;
                line-height: 1.7;
            }
        }
        
    }
    .setOut1{
        animation: myMou 1s;
        animation-fill-mode: forwards;
    }
    .setOut2{
        animation: myMou1 1s;
        animation-fill-mode: forwards;
    }
    >.sh-video-footer{
        position: absolute;
        bottom: 0;
        height: 80px;
        width: calc(100% - 26px);
        background-color: #ddd;
        opacity: 1;
        padding: 0 13px;
        >.sh-video-footer-top{
            p{
                position: relative;
                top: -21px;
                margin: 0;
                padding: 0;
                color: #fff;
                width: 100%;
                span:first-child{
                    position: absolute;
                    left: 0;
                }
                span:last-child {
                    position: absolute;
                    right: 0;
                }
            }
        }
        >.sh-video-footer-button{
            .bg-purple-light{
                .yinliang{
                    position: absolute;
                    width: 30px;
                    height: 100px;
                    top: -100px;
                    .block{

                    }
                }
            }
            ul{
                style-list: none;
                margin: 0;
                >li{
                    display: inline-block;
                    margin: 0 13px;
                    i{
                        font-size: 30px;
                    }
                    .icon-shoucang5{
                        color: red;
                    }
                    .icon-pingfen{
                        color: rgb(247, 186, 42);
                    }
                }
                .playbackRate-li{
                    width: 50px;
                    cursor: default;
                    i{
                        font-size: 20px;
                    }
                }
            }
        }
    }
}
@keyframes example {
  0% {
    position: absolute;
    left: 28px;
    top: -0px;
  }
  100% {
    position: absolute;
    left: 28px;
    top: 100px;
  }
}
@keyframes myMou {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes myMou1 {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
</style>


