<template>
  <div class="sh-detail">
    <el-container>
      <el-container>
        <el-header >
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item v-for="(itme,index) in activeItme" :key="index" v-if="itme"><a href="" @click.prevent="handleClick(itme.id,itme.lev)">{{ itme.name }}</a></el-breadcrumb-item>
          </el-breadcrumb>
        </el-header>
        <el-main>
          <Video v-if="flag1"/>
        </el-main>
      </el-container>
      <el-aside width="300px">
        <ShTab />
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import Video from '@v/tab/detail/shVideo'
import ShTab from '@v/tab/detail/shTab'
import {mapState,mapActions,mapMutations} from'vuex'
export default {
  name: 'Detail',
  components: {
    Video,
    ShTab
  },
  data () {
    return {
      activeName: 'first',
      flag1: false,
    }
  },
  computed: {
    ...mapState({
      ztid:state=> state.VideoData.activeZTID,
      activeItme:state=> state.VideoData.activeItme,
    }),
  },
  created(){
    //判断当前是否有权限
    if(!this.ztid){
      //判断是否存在传参
      if(this.$route.query.PID){
        //将传参值设置为当前项
        this.setactiveZTID(this.$route.query.PID);
      }else{
        this.$router.push('/main');
      }
    };
    //获取授权信息
    this.requestUserByZTID().then(r=>{
      if(r.data.flag){
        //获取路径信息
        this.getActiveItme();
        //获取数据信息
        this.requestDataByZTID().then(()=>{
          this.flag1 = true;
        });
      }else{
        this.$message.error({message: '无权限', duration: 3000 });
        this.$router.push('/main');
      }
    }).catch(r=>{});
  },
  methods:{
    ...mapMutations("VideoData", ["setactiveZTID","setactiveItme"]),
    ...mapActions("VideoData", ["requestUserByZTID","requestDataByZTID","getActiveItme"]),
    handleClick(val1, val2) {
      switch(val2) {
        case 1:
            this.$router.push('/main');
            break;
        case 2:
            //切换选择的元素
            this.$router.push({name:"main",query:{PID:val1}});
            break;
        case 3:
          //刷新当前页
          break;
        default:
          break;
      } 
    },

  }
}
</script>

<style scopend lang="less">
.sh-detail{
  height: calc(100vh - 80px - 61px);
  >.el-container{
    width: 100%;
    height: 100%; 
    >.is-vertical{
      >.el-header{
        height: 30px !important;
        padding-left: 0;
        >.el-breadcrumb{
          line-height: 2;
        }
      }
      >.el-main{
        padding: 0;
        margin: 0 20px 0 0;
      }
    }
    .el-aside{
      width: 100%;
      .el-tabs__item{
        width: calc(260px / 4);
      }
    }
  }
}
</style>



