// 判断字符串参数转换为对应类型是否为空，返回转换后的参数或希望的返回类型
export function ifStrPram (pram, type) {
    console.log(pram);
    switch (type){
        case 1://数组
            if(!pram){
                pram = [{"id":-1,"name":"首页",lev:1}];
            }else{
                pram = JSON.parse(pram);
            }
             break ;
        case 2://数字
             break ;
        case 3://字符串
             break ; 
        case 4://JSON对象
             break ;   
        case 5://null
             break ;   
        default://原样输出
             break ;   
    }
    return pram;
}
/*时间转换为标准*/
export function int2time(s){
     var hour = parseInt(s/3600);
     var minute = parseInt((s % 3600)/60);
     var second = Math.ceil(s%60);
     if (second == 60){
          second = 0;
          minute += 1; 
     }
     if (minute == 60){
          minute = 0;
          hour += 1; 
     }
     if (hour<10) {
          if (hour<0) {
               hour = 0;
          }
          hour = "0"+hour;
     }
     if (minute<10) {
          if (minute<0) {
               minute = 0;
          }
          minute = "0"+minute;
     }
     if (second<10) {
          if (second<0) {
               second = 0;
          }
          second = "0"+second;
     }
     if (isNaN(second)) {
          return false;
     } else {
          return hour + ":" + minute + ":" + second;
     }
}


