<template>
    <div>
      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple">
          <div id="main" style="width: 450px;height:300px;"></div>
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <div id="main1" style="width: 450px;height:300px;"></div>
        </div></el-col>
      </el-row>
      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple">
          <div id="main2" style="width: 450px;height:300px;"></div>
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <div id="main3" style="width: 450px;height:300px;"></div>
        </div></el-col>
      </el-row>
    </div>
</template>

<script>
import echarts from 'echarts';
export default {
  name: 'Test2',
  data () {
    return {
    }
  },
  methods: {
    drawPie () {
      echarts.init(document.getElementById('main')).setOption({
        title: {
          text: '月访问量',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
        },
        series: [
            {
                name: '访问来源',
                type: 'pie',
                radius: '50%',
                data: [
                    {value: 1048, name: '非活跃用户'},
                    {value: 735, name: '活跃用户'},
                    {value: 35, name: '其它用户'}
                ],
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }
        ]
      });
      echarts.init(document.getElementById('main1')).setOption({
          title: {
            text: '视频访问量',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['GUI功能函数(一)', '调试专题', '数据专家(二)', '板块管理(一)', '函数加密', 'BL模型(二)', '101因子库(一)']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 80, 70, 110, 130],
              type: 'bar'
          }]
      });
      echarts.init(document.getElementById('main2')).setOption({
          title: {
            text: '专题访问量',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['GUI功能函数', '调试专题', '数据专家', '板块管理', '函数加密', 'BL模型', '101因子库']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 80, 70, 110, 130],
              type: 'bar'
          }]
      });
      echarts.init(document.getElementById('main3')).setOption({
          title: {
            text: '系列访问量',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['产品介绍', '平台操作', '基础函数', '数据提取', '第三方交互', '业务框架']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 70, 110, 130],
              type: 'bar'
          }]
      });
    }
  },
  mounted(){
    this.$nextTick(function() {
      this.drawPie('main');
    });
  } 
}
</script>

<style>
</style>



