<template>
    <el-container class="sh-user-div">
        <el-aside width="210px">
          <el-menu
            class="el-menu-vertical-demo"
            :unique-opened="true"
            :default-active=actionIndex>
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-user"></i>
                <span>用户中心</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="1-1" @click="get(1)">授权列表</el-menu-item>
                <el-menu-item index="1-2" @click="get(1)">播放记录</el-menu-item>
                <el-menu-item index="1-3" @click="get(1)">预约记录</el-menu-item>
                <el-menu-item index="1-5" @click="get(1)">我的收藏</el-menu-item>
                <el-menu-item index="1-4" @click="get(1)">下载记录</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-setting"></i>
                <span>管理中心</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="2-1" @click="get(2)">视频授权</el-menu-item>
                <el-menu-item index="2-2" @click="get(4)">视频信息</el-menu-item>
                <el-menu-item index="2-3" @click="get(3)">预约中心</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-s-data"></i>
                <span>数据中心</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="3-1" @click="get(5)">统计中心</el-menu-item>
                <el-menu-item index="3-2" @click="get(6)">用户信息</el-menu-item>
                <el-menu-item index="3-3" @click="get(7)">视频信息</el-menu-item>
                <el-menu-item index="3-4" @click="get(8)">下载信息</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>其它</span>
              </template>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <userVideoList :activeIndex="actionIndex - 1" v-if="flag == 1"/>
          <userOpenVideo v-if="flag == 2"/>
          <subscribe v-if="flag == 3"/>
          <test v-if="flag == 4"/>
          <Test2 v-if="flag == 5"/>
          <UserListInfo v-if="flag == 6"/>
          <VideoListInfo v-if="flag == 7"/>
          <DownLoad v-if="flag == 8"/>
        </el-main>
    </el-container>
</template>

<script>
import userVideoList from '@v/tab/user/userVideoList';
import userOpenVideo from '@v/tab/user/userOpenVideo';
import subscribe from '@v/tab/user/subscribe';
import test from '@v/tab/user/test';
import Test2 from '@v/tab/user/test2';
import UserListInfo from '@v/tab/user/UserListInfo';
import VideoListInfo from '@v/tab/user/VideoListInfo';
import DownLoad from '@v/tab/user/DownLoad';


export default {
  name: 'User',
  components: {
    userVideoList,
    userOpenVideo,
    subscribe,
    test,
    Test2,
    UserListInfo,
    VideoListInfo,
    DownLoad
  },
  data () {
    return {
        flag:1,
        actionIndex: "1",
        edata:{
          title: {
            text: '视频访问量',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['GUI功能函数(一)', '调试专题', '数据专家(二)', '板块管理(一)', '函数加密', 'BL模型(二)', '101因子库(一)']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 80, 70, 110, 130],
              type: 'bar'
          }]
        }
    }
  },
  methods: {
      get(val){
        this.flag = val;
      }
  }
}
</script>

<style scopend lang="less">
.sh-user-div{
  >.el-aside{
    border-right: solid 1px #e6e6e6;
    >.el-menu{
      border: 0 !important;
    }
  }
  >.el-main{
    padding: 0;
    height: calc(100vh - 80px - 61px);
  }
}
</style>



