<template>
    <div class="download">
        <Table :columns="columns" :data="data" size="small" ref="table"></Table>
    </div>
</template>

<script>
export default {
  name: 'DownLoad',
  data () {
    return {
        columns: [
            {
                title: '文件名',
                key: 'name'
            },
            {
                title: '所属专题',
                key: 'ztname'
            },
            {
                title: '管理用户',
                key: 'uid'
            },
            {
                title: '更新日期',
                key: 'date'
            },
            {
                title: '下载次数',
                key: 'count'
            },
            {
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('Button', {
                        on: {
                            click: () => {
                                alert(params.index + '暂不提供下载');
                            }
                        }
                    }, '下载')
                }
            }
        ],
        data:[
            {
                name: 'test.txt',
                ztid: 12,
                ztname: '天软数据介绍',
                uid: 'tssh',
                date: '2020-01-02',
                count: 100
            },
            {
                name: 'test.funtsl',
                ztid: 12,
                ztname: '天软数据介绍',
                uid: 'tssh',
                date: '2020-01-02',
                count: 100
            }
        ]
    }
  },
  methods: {
      
  }
}
</script>

<style>
</style>



