<template>
    <div class="VideoListInfo">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="专题列表" name="first">
                <Table :columns="columns1" :data="data1" size="small" ref="table"></Table>
            </el-tab-pane>
            <el-tab-pane label="视频列表" name="second">
                <Table :columns="columns2" :data="data2" size="small" ref="table"></Table>
            </el-tab-pane>
        </el-tabs>
        <Modal class="ShModel" 
            title="视频详情信息"
            v-model="flag"
            :closable="false">
            <p>视频ID</p>
            <p>视频名</p>
            <p>收藏用户</p>
            <p>观看用户</p>
            <p>用户评价</p>
            <p>添加标签</p>
            <p>首页推广</p>
        </Modal>
    </div>
</template>

<script>
export default {
  name: 'VideoListInfo',
  data () {
    return {
         flag: false,
         activeName: 'first',
         columns1:[
            {
                title: '专题名称',
                key: 'ztname'
            },
            {
                title: '最近播放',
                key: 'date',
                "sortable": true
            },
            {
                title: '点击次数',
                key: 'status',
                "sortable": true
            },
            {
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('Button', {
                        on: {
                            click: () => {
                                this.flag = true;
                            }
                        }
                    }, '查看')
                }
            }
        ],
         columns2:[
            {
                title: '视频名称',
                key: 'videoname'
            },
            {
                title: '最近播放',
                key: 'date',
                "sortable": true
            },
            {
                title: '播放次数',
                key: 'status',
                "sortable": true
            },
            {
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('Button', {
                        on: {
                            click: () => {
                                this.flag = true;
                            }
                        }
                    }, '查看')
                }
            }
        ],
         data1:[
            {
                ztname: '天软数据介绍',
                videoname: '天软数据介绍（一）',
                date: '2021-08-06',
                status: 2
            },{
                ztname: '天软数据介绍',
                videoname: '天软数据介绍（一）',
                date: '2021-08-06',
                status: 10
            }
        ],
         data2:[
            {
                ztname: '天软数据介绍',
                videoname: '天软数据介绍（一）',
                date: '2021-08-06',
                status: 2
            },{
                ztname: '天软数据介绍',
                videoname: '天软数据介绍（一）',
                date: '2021-08-06',
                status: 10
            }
        ],
    }
  },
  methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
  }
}
</script>

<style>
</style>



