<template>
  <div class="content_top">
    <el-tabs v-model="activeID" @tab-click="handleClick">
        <el-tab-pane v-for="(itme,index) in data" :label="itme.name" :name="itme.id" :key="index"></el-tab-pane>
    </el-tabs>
    <ShInlineBlockSeminar/>
  </div>
</template>
<script>
import {mapState,mapGetters,mapActions,mapMutations} from'vuex'
import ShInlineBlockSeminar from '@v/plugin/shInlineBlockSeminar'
export default {
  name: 'tabsmenu',
  components: {
    ShInlineBlockSeminar
  },
  data () {
    return {
      activeID: "0",
    }
  },
  created(){
    let _this = this;
    //判断是否存在传参
    if(this.$route.query.PID){
      //
      this.activeID = this.$route.query.PID + "";
    }
    function time(){
      if(_this.data.length){
        //执行
        _this.requestVideoZTlistByID({_post:{"xlid":_this.activeID,"optype":5}});
        return ;
      }else{
        setTimeout(time,100);
      }
    }
    time();
  },
  computed: {
    ...mapState({//获取课程分类列表
      data:state=>state.VideoData.CourseClassListCope
    }),
  },
  methods: {
    ...mapActions("VideoData", [
      "requestVideoZTlistByID",
    ]),
    handleClick(tab, event) {
      this.requestVideoZTlistByID({_post:{"xlid":this.activeID,"optype":5}});
    }
  }
}
</script>

<style scopend lang="less">
.content_top{
  .el-tabs__nav-wrap{
    text-align: center;
    >.el-tabs__nav-scroll{
      width: 74%;
      margin: 0 auto;
    }
  }
}
</style>



