<template>
  <ul class="sh-InlineBlock-seminar-ul">
      <li v-for="(itme,index) in Data" :key="index"  class="seminar-li" @click="getVideo(itme.ID,itme.Name,itme.Perm)">
          <div class="seminar-top">
              <span v-if="!itme.Perm">预约</span>
              <img v-bind:src="'http://www.tinysoft.com.cn/tsdn/tstrain/resource/img/catalog_img/'+itme.img" />
          </div>
          <div class="seminar-content">
              <p class="seminar-content-p1">
                <span class="seminar-content-p1-span1">{{itme.Name}}</span>
                <span class="seminar-content-p1-span2">{{itme.ModifyDate}}</span>
              </p>
              <p class="seminar-content-p2">
                <span  class="el-icon-video-play">{{ itme.count }}</span>
                <span  class="el-icon-view">{{ itme.playCount }}</span>
              </p>
              <p class="seminar-content-p3">{{ itme.Describe }}</p>
          </div>
      </li>
  </ul>
</template>

<script>
import {mapState,mapMutations} from'vuex'
export default {
  name: 'shInlineBlockSeminar',
  data () {
    return {
    }
  },
  computed: {
    ...mapState({//获取数据
      Data:state=> state.VideoData.videoZTList,
      activeItme:state=> state.VideoData.activeItme,
    }),
  },
  methods: {
    ...mapMutations("VideoData", ["setactiveZTID","setactiveItme"]),
    getVideo(val1,val2,val3){
      if(val3){
        this.setactiveZTID(val1);
        this.activeItme[2] = {"id":val1,"name":val2,lev:3};
        this.setactiveItme(this.activeItme);
        this.$router.push({name:"Detail",query:{PID:val1}});
      }else{
        this.$message.success({message: '提示预约', duration: 1000 });
      }
    }
  }
}
</script>

<style scopend lang="less">
ul.sh-InlineBlock-seminar-ul{
  list-style: none;
  text-align: left;
  padding: 0px;
  >li.seminar-li{
    display: inline-block;
    border-radius: 10px;
    width: 222px;
    height: 237px;
    margin: 10px 14px;
    overflow: hidden;
    >div.seminar-top{
      width: 100%;
      height: 153px;
      border: 1px solid #ddd;
      position: relative;
      border-radius: 7px;
      overflow: hidden;
      >span{
        position: absolute;
        left: 0px;
        background-color: red;
        color: #fff;
        font-size: 14px;
        width: 35px;
        height: 25px;
        border-radius: 0 0 7px 0;
      }
      >img{
        width: 100%;
        height: 100%;
      }
    }
    >div.seminar-content{
      >p{
        margin: 0;
      }
      >p.seminar-content-p1{
        >span{

        }
        .seminar-content-p1-span1{
          font-size: 16px;
          color: #555;
          font-weight: bold;
          font-family: serif;
          max-width: 60%;
          display: inline-block;
          overflow-x: hidden;
          white-space: nowrap;
        }
        .seminar-content-p1-span2{
          color: #777a7e;
          float: right;
          font-size: 12px;
        }
      }
      >p.seminar-content-p2{
        >span{
          color: #777a7e;
          font-size: 12px;
          padding-left: 2px;
          vertical-align: middle;
          margin-right: 13px;
        }
      }
      >p.seminar-content-p3{
        color: #777a7e;
        font-size: 12px;
        position: relative; 
        line-height: 19px; 
        max-height: 40px;
        overflow: hidden;
        width: 100%;
      }
      p.seminar-content-p3::after{
        content: "..."; 
        position: absolute; 
        bottom: 0; 
        right: 0; 
        padding-left: 20px;
        background: -webkit-linear-gradient(left, transparent, #fff 55%);
        background: -o-linear-gradient(right, transparent, #fff 55%);
        background: -moz-linear-gradient(right, transparent, #fff 55%);
        background: linear-gradient(to right, transparent, #fff 55%);
      }
    }
  }
}
</style>



