import { requestPost} from "../untils/list.js";
import { ifStrPram } from "../untils/tool.js";

// state
const state = {
    CourseClassList: [],//课程分类列表
    CourseClassListCope: [],//课程分类列表
    videoList:[],//条件筛选后的结果
    tagData: [],//所有筛选条件
    checkedCities: null,//选中的筛选条件
    videoListCount: 0,//查询总记录条数
    videoZTList: [],//系列下专题视频列表
    activeZTID: null,//专题ID
    videoDataInfo: [],//视频列表
    videoActiveIndex: 0,//切换视频数组下标
    flag1: true,  //显示播放或暂停
    activeXLobj: {},
    activeItme: [],//当前专题路径
};
// mutations
const mutations = {
    setvideoList(state, item){
        state.videoList = item;
    },
    setcheckedCities(state, item){
        state.checkedCities = item;
    },
    setactiveZTID(state, item){
        state.activeZTID = item;
    },
    setvideoActiveIndex(state, item){
        state.videoActiveIndex = item;
        state.flag1 = false;
        state.activeItme[3] = {id:state.videoDataInfo[item].ID,lev:4,name:state.videoDataInfo[item].Name};
        state.activeItme = Object.assign([],state.activeItme);
    },
    setflag1(state, item){
        state.flag1 = item;
    },
    setflag4(state){
        state.videoDataInfo[state.videoActiveIndex].sc = !state.videoDataInfo[state.videoActiveIndex].sc;
    },
    setflag5(state){
        state.videoDataInfo[state.videoActiveIndex].dz = !state.videoDataInfo[state.videoActiveIndex].dz;
    },
    setactiveXLobj(state, item){
        state.activeXLobj = item;
    },
    setactiveItme(state, item){
        state.activeItme = item;
    }
};
// actions
const actions = {
  getCourseClassList({ state }){
    return new Promise((resolve, reject) => {
      requestPost("Api1",{"optype":1}).then((r)=>{
        if(r.data.flag){
          state.CourseClassList = r.data.data;
          let arr = [];
          for (var i=0;i<state.CourseClassList.length;i++){
            arr.push({"id":state.CourseClassList[i].id + "","name":state.CourseClassList[i].name});
          }
          arr.splice(1, 0, {"id":"0","name":"公开视频"});
          state.CourseClassListCope = arr;
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
  requestTagData({ state }){
    return new Promise((resolve, reject) => {
      requestPost("Api1",{"optype":2}).then((r)=>{
        if(r.data.flag){
          state.tagData = r.data.data;
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
  requestVideolistPage({ state }, { _post }){
    _post["checkedList"] = state.checkedCities;
    return new Promise((resolve, reject) => {
      requestPost("Api1",_post).then((r)=>{
        if(r.data.flag){
          state.videoList = r.data.data;
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
  requestVideolistCount({ state }){
    return new Promise((resolve, reject) => {
      requestPost("Api1",{"checkedList":state.checkedCities,"optype":4}).then((r)=>{
        if(r.data.flag){
          state.videoListCount = r.data.data[0]["COUNT"];
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
  requestVideoZTlistByID({ state },{_post}){
    let index = parseInt(_post["xlid"]);
    if(index == 0){return ;}
    return new Promise((resolve, reject) => {
      requestPost("Api1",_post).then((r)=>{
        if(r.data.flag){
          state.videoZTList = r.data.data;
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
  requestUserByZTID({ state }){
    return new Promise((resolve, reject) => {
      requestPost("Api1",{"ztid":state.activeZTID,"optype":7}).then((r)=>{
        resolve(r);
      }).catch(() => {
        reject();
      });
    });
  },
  requestDataByZTID({ state }){
    return new Promise((resolve, reject) => {
      requestPost("Api1",{"ztid":state.activeZTID,"optype":6}).then((r)=>{
        if(r.data.flag){
          state.videoDataInfo = r.data.data;
          state.flag4 = state.videoDataInfo[0].sc;
          state.flag5 = state.videoDataInfo[0].dz;
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
  getActiveItme({ state }){
    return new Promise((resolve, reject) => {
      requestPost("Api1",{"ztid":state.activeZTID,"optype":8}).then((r)=>{
        if(r.data.flag){
          console.log(r.data.data);
          state.activeItme = r.data.data;
          resolve(r.data);
        }else{
          reject();
        }
      }).catch(() => {
        reject();
      });
    });
  },
};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
};


