import { requestPost,requestPostByUser} from "../untils/list.js";
import { ifStrPram } from "../untils/tool.js";

// state
const state = {
    
};
// mutations
const mutations = {
    
};
// actions
const actions = {
    RequestLogin({ state }){
        return new Promise((resolve, reject) => {
            requestPost("ApiUser",{"optype": "login","userid":"tsshanghang","pwd":"shanghang"}).then((r)=>{
                if(r.data.flag){
                    resolve(r.data);
                }else{
                    reject();
                }
            }).catch(() => {
                reject();
            });
        });
    },
    RequestLogout({ state }){
        return new Promise((resolve, reject) => {
            requestPost("ApiUser",{"optype": "logout"}).then((r)=>{
                if(r.data.flag){
                    resolve(r.data);
                }else{
                    reject();
                }
            }).catch(() => {
                reject();
            });
        });
    },
};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
};


