<template>
    <div class="UserListInfo">
      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple search">
          <Input class="ts-search-noborder" :style="searchStyle" search clearable placeholder="检索当前组用户..." />
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <el-checkbox-group v-model="checkList">
            <el-checkbox label="试用"></el-checkbox>
            <el-checkbox label="非试用"></el-checkbox>
            <el-checkbox label="TS"></el-checkbox>
            <el-checkbox label="非TS"></el-checkbox>
          </el-checkbox-group>
        </div></el-col>
      </el-row>
      <Table :columns="columns8" :data="data7" size="small" ref="table"></Table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
      <Modal class="ShModel" 
        title="用户详情信息"
        v-model="flag"
        :closable="false">
        <p>用户ID</p>
        <p>用户名</p>
        <p>邮箱</p>
        <p>账号类型</p>
        <p>账号创建日期</p>
        <p>账号到期日期</p>
        <p>账号负责人ID</p>
        <p>账号所属公司</p>
        <p>收藏列表</p>
        <p>观看列表</p>
        <p>检索列表</p>
        <p>产品推荐 账号提示 邮件提示</p>
      </Modal>
    </div>
</template>

<script>
export default {
  name: 'UserListInfo',
  components: {
  },
  data () {
    return {
      searchStyle: {
        width: '200px',
        marginLeft: '16px',
        marginTop: '3px'
      },
      checkList: [],
      columns8: [
          {
              "title": "活跃度",
              "key": "activity",
              "width": 150,
              "sortable": true,
              filters: [
                  {
                      label: '周排行',
                      value: 1
                  },
                  {
                      label: '月排行',
                      value: 2
                  }
              ],
              filterMultiple: false,
              filterMethod (value, row) {
                  if (value === 1) {
                      return row.show > 4000;
                  } else if (value === 2) {
                      return row.show < 4000;
                  }
              }
          },
          {
              "title": "USERID",
              "key": "uid",
              "width": 150,
          },
          {
              "title": "姓名",
              "key": "uname",
              "width": 150,
          },
          {
              "title": "所属公司",
              "key": "company",
              "width": 165,
          },
          {
              "title": "账号类型",
              "key": "utype",
              "width": 150,
          },
          {
              "title": "负责人ID",
              "key": "mid",
              "width": 150,
          },
          {
              title: '操作',
              key: 'action',
              width: 150,
              align: 'center',
              render: (h, params) => {
                  return h('Button', {
                      on: {
                          click: () => {
                            this.flag = true;
                          }
                      }
                  }, '详情')
              }
          }
      ],
      data7: [
        {
          activity: 1,
          uid: 'tswxh',
          uname: '王小虎',
          company: 'TS',
          utype: '试用',
          mid: 'ts',
          address: '上海市普陀区金沙江路 1518 弄'
        }],
      flag: false,
    }
  },
  methods: {
      handleEdit(val1,val2){
        this.flag = true;
      }
  }
}
</script>

<style scopend lang="less">
.UserListInfo{
  .el-row{
    line-height: 33px;
    border-bottom: 1px solid #ddd;
    .search{
      text-align: left;
      .ts-search-noborder{
      }
    }
  }
}
</style>



