import Vue from'vue'
import Vuex from'vuex'
Vue.use(Vuex)
import VideoData from './videoData.js'
import Users from './Users.js'

let store=new Vuex.Store({
    modules: {
        VideoData,
        Users,
    }
})

/** */
export default store;


