<template>
    <el-tabs type="border-card" class="sh-video-tabs">
        <el-tab-pane label="目录">
            <div>
                <el-row v-for="(itme,index1) in videoList" :key="index1" :title="itme.Name" @click.native="setvideoIndex(index1)">
                    <el-col :span="6"><div class="grid-content bg-purple">
                        <el-tag>课时{{ index1 + 1 }}</el-tag>
                    </div></el-col>
                    <el-col :span="16"><div class="grid-content bg-purple-light">
                        {{ itme.Name }}
                    </div></el-col>
                    <el-col :span="2"><div class="grid-content bg-purple">
                        <el-badge :value="itme.count" class="item" type="primary"></el-badge>
                    </div></el-col>
                </el-row>
            </div>
        </el-tab-pane>
        <el-tab-pane label="资料">
            <el-collapse accordion>
                <el-collapse-item>
                    <template slot="title">
                    PPT
                    </template>
                    <div>
                        <ul>
                            <li>
                                test.txt
                            </li>
                        </ul>
                    </div>
                </el-collapse-item>
            </el-collapse>
        </el-tab-pane>
        <el-tab-pane label="介绍">
            <div>
                <span>课程评价</span>
                <span v-for="i in 3" class="iconfont icon-pingfen"></span>
                <span v-for="j in 2" class="iconfont icon-shouc"></span>
                <el-divider></el-divider>
                <span>青春是一个短暂的美梦, 当你醒来时, 它早已消失无踪</span>
                <el-divider></el-divider>
                <el-row v-for="i in 20" :key="i">
                    <el-col :span="4"><div class="grid-content bg-purple">
                        user
                        <span class="iconfont icon-user"></span>
                    </div></el-col>
                    <el-col :span="20"><div class="grid-content bg-purple-light">
                        <div>
                            <span v-for="j in 3" class="iconfont icon-pingfen"></span>
                            <span v-for="k in 2" class="iconfont icon-shouc"></span>
                            <span style="font-size: 12px;
    color: #788084;">2021-08-16</span>
                        </div>
                        <div :class="{'show_pinglun':current_index==i,'pinglun':1}" @click="cg_current_index(i)">
                            {{ text }}
                        </div>
                    </div></el-col>
                </el-row>
            </div>
        </el-tab-pane>
    </el-tabs>
</template>
<script>
import {mapState,mapMutations} from'vuex'
export default {
  name: 'shTab',
  data () {
    return {
        value1: 3,
        text: "评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容",
        current_index: -1,
    }
  },
  computed: {
    ...mapState({
      videoList: state=> state.VideoData.videoDataInfo,
      videoIndex: state=> state.VideoData.videoActiveIndex,
      fileList: [],
    }),
  },
  methods: {
    ...mapMutations("VideoData", ["setvideoActiveIndex"]),
    cg_current_index(index) {
        this.current_index = index;
    },
    setvideoIndex(val) {
        this.setvideoActiveIndex(val);
    }
  }
}
</script>

<style scopend lang="less">
.sh-video-tabs{
    height: calc( 100% - 2px);
    overflow: hidden;
    >.el-tabs__header{
        .el-tabs__nav{
            width: 100%;
            .el-tabs__item{
                width: calc( 100% / 3 + 2px );

            }
        }
    }
    >.el-tabs__content{
        height: calc( 100% - 70px);
        width: calc( 100% - 15px);
        overflow-y: scroll;
        #pane-0{
            .el-row{
                margin: 13px 0;
                .bg-purple-light{
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
            .el-row:hover{
                width: calc( 100% + 3px);
                height: calc( 100% + 3px);
            }
        }
        ul{
            list-style: none;
            padding: 0;
            li{
                text-align: left;
                margin: 7px auto;
                span:first-child{
                    margin-right: 13px;
                }
                span:last-child{
                    margin-left: 13px;
                }
            }
        }
        #pane-2{
            .el-row{
                margin: 10px auto;
                .pinglun{
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 3;
                    overflow: hidden;
                    font-size: 14px;
                    color: #788084;
                    cursor:pointer;
                }
                .show_pinglun{
                    display: block !important;
                }
            }
            .icon-pingfen{
                color: #f7ba2a;
            }
        }
    }
}
</style>


