import Vue from 'vue'
import VueRouter from 'vue-router'
import Vmain from '@v/tab/vmain'
import Major from '@v/tab/major'
import User from '@v/tab/user'
import Detail from '@v/tab/Detail'

Vue.use(VueRouter)

/**
 * @描述：获取菜单信息，生成路由列表
*/
const routes = [
    { path: '/', redirect: '/main'},
    { name: 'main', path: '/main', component: Vmain},
    { name: 'Detail', path: '/Detail', component: Detail},
    { path: '/major', component: Major},
    { path: '/user', component: User}
]

const router = new VueRouter({
    mode: 'history',
    routes
})

export default router


