<template>
    <div>
        <el-table
            :data="tableData"
            style="width: 100%;margin-bottom: 20px;"
            row-key="id"
            border
            default-expand-all
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
            <el-table-column
            prop="name"
            label="名称"
            width="200">
            </el-table-column>
            <el-table-column
            prop="date"
            label="创建日期"
            width="150">
            </el-table-column>
            <el-table-column
            prop="address"
            sortable
            width="100"
            label="类型">
            </el-table-column>
            <el-table-column
            prop="flag"
            sortable
            width="100"
            label="授权">
            </el-table-column>
            <el-table-column
            prop="date"
            label="修改日期"
            width="150">
            </el-table-column>
            <el-table-column
                label="操作"
                align="content">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">类型授权</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">用户授权</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
  name: 'userOpenVideo',
  data () {
    return {
        tableData: [{
          id: 1,
          date: '2016-05-02',
          name: 'TSL语言',
          address: '系列',
          flag: '公开'
        }, {
          id: 2,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          flag: '公开'
        }, {
          id: 3,
          date: '2016-05-01',
          name: '基础函数',
          address: '系列',
          flag: '公开',
          children: [{
              id: 31,
              date: '2016-05-01',
              name: '矩阵专题',
              address: '专题',
          flag: '公开',
              children: [{
                id: 311,
                date: '2016-05-01',
                name: '矩阵专题(一)',
                address: '视频',
                flag: '公开',
                }, {
                id: 312,
                date: '2016-05-01',
                name: '矩阵专题(二)',
                address: '视频',
                flag: '公开'
              }]
            }, {
              id: 32,
              date: '2016-05-01',
              name: '语言基础',
              address: '专题',
                flag: '公开'
          }]
        }, {
          id: 4,
          date: '2016-05-03',
          name: '业务框架',
          address: '系列',
                flag: '公开'
        },{
          id: 5,
          date: '2016-05-02',
          name: 'TSL语言',
          address: '系列',
          flag: '公开'
        }, {
          id: 6,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          flag: '公开'
        }, {
          id: 7,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          flag: '公开'
        }, {
          id: 8,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          flag: '公开'
        }, {
          id: 9,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          flag: '公开'
        } ]
    }
  },
  methods: {
      handleEdit(val1,val2){
          console.log(val1,val2);
          this.$alert('确认修改', '类型授权', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      handleDelete(val1,val2){
          console.log(val1,val2);
          this.$alert('确认修改', '用户授权', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      }
  }
}
</script>

<style>
</style>



