<template>
    <div>
        <Table :columns="columns1" :data="data2"></Table>
    </div>
</template>

<script>
export default {
  name: 'subscribe',
  data () {
    return {
        columns1: [
            {
                title: '用户ID',
                key: 'userid'
            },
            {
                title: '用户名',
                key: 'username'
            },
            {
                title: '预约名称',
                key: 'ztname'
            },
            {
                title: '预约类型',
                key: 'videoname'
            },
            {
                title: '预约日期',
                key: 'regdate',
                "sortable": true
            },
            {
                title: '审核状态',
                key: 'status',
                "sortable": true
            },
            {
                title: '完成日期',
                key: 'enddate',
            },
            {
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('Button', {
                        on: {
                            click: () => {
                                alert(params.index + '暂不提供此功能');
                            }
                        }
                    }, '审核')
                }
            }
        ],
        data2: [
            {
                userid: 'test1',
                username: '测试名',
                ztname: '天软数据介绍',
                videoname: '专题',
                regdate: '2021-08-01',
                status: '已预约',
                enddate: '2021-08-06'
            },{
                userid: 'test1',
                username: '测试名',
                ztname: '天软数据介绍（一）',
                videoname: '视频',
                regdate: '2021-08-01',
                status: '待审核',
                enddate: '2021-08-06'
            },{
                userid: 'test1',
                username: '测试名',
                ztname: '数据介绍',
                videoname: '系列',
                regdate: '2021-08-01',
                status: '已通过',
                enddate: '2021-08-06'
            }
        ]
    }
  },
  methods: {
      
  }
}
</script>

<style>
</style>



