import axios from '../untils/http'
import QS from 'qs'
import base from './base'
import { offLineConfirm } from './au.js'
let ifWarnOffLine = true; //启用超时验证

//格式化请求参数
function formatRequestData (data) {
    let _data = {}
    for (let n in data) {
        _data[n] = typeof data[n] === 'object' ? JSON.stringify(data[n]) : data[n];
    }
    return _data
}

/**
 * post方法，对应post请求
 * @desc注册请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function requestPostByUser(fun,data) {
    return axios({
        url: base+`/api/api.tsl`,
        method: 'post',
        data: QS.stringify({ __datafun__: fun, ...formatRequestData(data) })
    })
}
/**
 * get方法，对应get请求
 * @desc登录请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function requestGetByUser() {
    return axios({
        url: `/api/api.tsl`,
        method: 'get'
    })
}
/***  无需进行登录验证的请求 ****/
/**
 * post方法，对应post请求
 * @desc注册请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
 export function requestPost(fun,data) {
    return axios({
        url: base+`/api/test.tsl`,
        method: 'post',
        data: QS.stringify({ __datafun__: fun, ...formatRequestData(data) })
    })
}
/**
 * get方法，对应get请求
 * @desc登录请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function requestGet() {
    return axios({
        url: `/api/test.tsl`,
        method: 'get'
    })
}


