<template>
    <div>
        <el-row>
            <el-col :span="20"><div class="grid-content bg-purple">
                视频修改
            </div></el-col>
            <el-col :span="4"><div class="grid-content bg-purple-light">
                删除视频<i class="el-icon-delete"></i>
                新增系列<i class="el-icon-plus"></i>
            </div></el-col>
        </el-row>
        <el-table
            :data="tableData"
            style="width: 100%;margin-bottom: 20px;"
            row-key="id"
            border
            default-expand-all
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
            <el-table-column
            type="selection"
            width="55">
            </el-table-column>
            <el-table-column
            prop="id"
            sortable
            width="100"
            label="排序">
            </el-table-column>
            <el-table-column
            prop="name"
            label="名称"
            width="200">
            </el-table-column>
            <el-table-column
            prop="date"
            label="创建日期"
            width="150">
            </el-table-column>
            <el-table-column
            prop="date"
            label="修改日期"
            width="150">
            </el-table-column>
            <el-table-column
            prop="username"
            label="管理用户"
            width="150">
            </el-table-column>
            <el-table-column
                label="操作"
                fixed="right"
                align="content">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">修改</el-button>
                    <el-button v-if="scope.row.id < 100"
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">新增</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
  name: 'test',
  data () {
    return {
        tableData: [{
          id: 1,
          date: '2016-05-02',
          name: 'TSL语言',
          address: '系列',
          username:'test',
        }, {
          id: 2,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          username:'test',
        }, {
          id: 3,
          date: '2016-05-01',
          name: '基础函数',
          address: '系列',
          username:'test',
          children: [{
              id: 31,
              date: '2016-05-01',
              name: '矩阵专题',
              address: '专题',
          username:'test',
              children: [{
                id: 311,
                date: '2016-05-01',
                name: '矩阵专题(一)',
                address: '视频',
          username:'test',
                }, {
                id: 312,
                date: '2016-05-01',
                name: '矩阵专题(二)',
                address: '视频',
          username:'test8',
              }]
            }, {
              id: 32,
              date: '2016-05-01',
              name: '语言基础',
              address: '专题',
          username:'test7',
          }]
        }, {
          id: 4,
          date: '2016-05-03',
          name: '业务框架',
          address: '系列',
          username:'test6',
        },{
          id: 5,
          date: '2016-05-02',
          name: 'TSL语言',
          address: '系列',
          username:'test5',
        }, {
          id: 6,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          username:'test4',
        }, {
          id: 7,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          username:'test3',
        }, {
          id: 8,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          username:'test2',
        }, {
          id: 9,
          date: '2016-05-04',
          name: '平台操作',
          address: '系列',
          username:'test1',
        } ]
    }
  },
  methods: {
      handleEdit(val1,val2){
          console.log(val1,val2);
          this.$alert('确认修改', '类型授权', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      handleDelete(val1,val2){
          console.log(val1,val2);
          this.$alert('确认修改', '用户授权', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      }
  }
}
</script>

<style>
</style>



