<template>
  <div class="seminar">
    <ul class="seminar-ul">
      <li class="seminar-li" v-for="(itme,index) in seminarData" :key="itme.id">
        <div class="seminar-li-left">
          <span>{{ itme.status?'公开':''}}</span>
          <img v-bind:src="'http://www.tinysoft.com.cn/tsdn/tstrain/resource/img/catalog_img/'+itme.img" />
        </div>
        <div class="seminar-li-right">
          <p class="seminar-title">{{ itme.title}}</p>
          <p>
            <el-button-group>
              <el-button type="primary" >简介</el-button>
              <el-button type="primary">课程预览</el-button>
            </el-button-group>
          </p>
          <p class="seminar-content">{{ itme.content}}</p>
        </div>
      </li>
    </ul>
    <el-pagination v-if="seminarData.length"
      class="seminar-page"
      @current-change="handleCurrentChange"
      background
      layout="prev, pager, next"
      :total="videoListCount">
    </el-pagination>
  </div>
</template>

<script>
import {mapState,mapActions} from'vuex'
export default {
  name: 'Seminar',
  data () {
    return {

    }
  },
  computed: {
    ...mapState({//获取数据
      seminarData:state=>state.VideoData.videoList,
      videoListCount:state=>state.VideoData.videoListCount,
    }),
    
  },
  methods: {
    ...mapActions("VideoData", [
      "requestVideolistPage",
    ]),
    handleCurrentChange(val){
      console.log(val);
      this.requestVideolistPage({_post:{"optype":3,"page":val}});
    }
  }
}
</script>

<style scopend lang="less">
.seminar{
  >.seminar-ul{
    list-style: none;
    padding: 0px;
    >.seminar-li:hover{
      background-color: #dddddd3b;
    }
    >.seminar-li{
      border-bottom: 1px solid #ddd;
      height: 150px;
      padding: 7px 0;
      >div{
        display: inline-block;
        overflow: hidden;
        height: 100%;
      }
      >.seminar-li-left{
        width: 248px;
        border: 1px solid #ddd;
        position: relative;
        border-radius: 7px;
        >span{
          position: absolute;
          left: 0px;
          background-color: red;
          color: #fff;
          font-size: 14px;
          width: 35px;
          height: 25px;
          border-radius: 0 0 7px 0;
        }
        >img{
          width: 100%;
          height: 100%;
        }
      }
      >.seminar-li-right{
        width: calc(100% - 255px);
        text-align: left;
        >.seminar-title{
          font-weight: 600;
          font-size: 17px;
        }
        >p{
          margin: 0px;
          >.el-button-group>.el-button--primary{
            width: 98px;
          }
        }
      }
    }
  }
}
p.seminar-content{
  position: relative; 
  line-height: 20px; 
  max-height: 60px;
  overflow: hidden;
}
p.seminar-content::after{
  content: "..."; 
  position: absolute; 
  bottom: 0; 
  right: 0; 
  padding-left: 40px;
  background: -webkit-linear-gradient(left, transparent, #fff 55%);
  background: -o-linear-gradient(right, transparent, #fff 55%);
  background: -moz-linear-gradient(right, transparent, #fff 55%);
  background: linear-gradient(to right, transparent, #fff 55%);
}
.seminar-page{
  margin: 13px;
}
</style>



