<template>
    <div>
        <Table :columns="columns" :data="data"></Table>
    </div>
</template>

<script>
import axsData from '@v/tab/user/user';
export default {
  name: 'userVideoList',
  props: [ 'activeIndex' ],
  data () {
    return {
        columns: [],
        data: [],
    }
  },
  created () {
    this.columns = axsData[this.activeIndex].domData;
    
    this.data = axsData[this.activeIndex].data;
  },
  methods: {
      
  }
}
</script>

<style>
</style>



