<template>
  <ul class="SelectTag-ul">
    <li v-for="(itme,index) in tagData" class="SelectTag-li" :key="itme.id">
      <span class="SelectTag-title">{{itme.name}} :</span>
      <el-checkbox v-model="item_v.checked" v-for="(item_v, index_v) in itme.values" :key="item_v.id" @change="((label)=>{changeBarStatus(label, itme.id, item_v.id)})">{{ item_v.name }}</el-checkbox>
    </li>
  </ul>
</template>

<script>
import {mapState,mapActions,mapMutations}from'vuex'
export default {
  name: 'SelectTag',
  data () {
    return {
      checkedCities:[],
    }
  },
  computed: {
    ...mapState({//获取数据
      tagData:state=>state.VideoData.tagData,
    }),
  },
  created(){
    this.requestTagData();
  },
  methods: {
      ...mapMutations("VideoData", ["setcheckedCities"]),
      ...mapActions("VideoData", [
        "requestTagData",
        "requestVideolistCount",
        "requestVideolistPage",
      ]),
      changeBarStatus(val,t1,t2){
        if (val){
          this.checkedCities.push([t1,t2]);
        }else{
          var index = -1; 
          for(var i=0;i<this.checkedCities.length;i++){
            if(this.checkedCities[i][0] == t1 && this.checkedCities[i][1] == t2 ){
              index = i; 
              break;
            }
          }
          if (index > -1) { 
            this.checkedCities.splice(index, 1); 
          } 
        }
        this.setcheckedCities(this.checkedCities);
        this.requestVideolistCount();
        this.requestVideolistPage({_post:{"optype":3,"page":1}});
      }
  }
}
</script>

<style scopend lang="less">
ul.SelectTag-ul{
  list-style: none;
  padding: 0;
  >li.SelectTag-li{
    margin: 10px 7px;
    text-align: left;
    span.SelectTag-title{
      font-weight: 600;
    }
  }
}
</style>



